import PageTitle from '../../common/PageTitle/PageTitle';

const About = () => {
  return (
    <>
      <PageTitle>O mionikach miasta Lesko i okolic</PageTitle>
      <p>
        Lesko to mae miasto w poudniowo-wschodniej Polsce, pooone nad rzek San. Jest to miejsce o bogatej historii
        i kulturze, ktre przyciga wielu turystw i mionikw przyrody. Lesko i okolice oferuj wiele atrakcji dla
        kadego, kto szuka odpoczynku i inspiracji. Mona tu podziwia zabytkow architektur, zwiedza muzea i galerie,
        spacerowa po malowniczych szlakach, a nawet sprbowa swoich si w spywie kajakowym lub lotniarstwie. Lesko to
        take miasto ludzi otwartych i yczliwych, ktrzy chtnie dziel si swoj pasj i wiedz o swoim regionie.
        Mionicy Leska i okolic tworz spoeczno ludzi zwizanych z tym miejscem nie tylko geograficznie, ale take
        emocjonalnie. S to ludzie, ktrzy ceni sobie tradycj i kultur, ale take s otwarci na nowe doznania i
        wyzwania. Mionicy Leska i okolic to ludzie, ktrzy kochaj swoje miasto i chc je pokaza innym.
      </p>
    </>
  );
};

export default About;
